/* General Navbar */
.navbar {
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 1000;
    background: rgba(0, 123, 255, 0.95); /* Semi-transparent blue */
    backdrop-filter: blur(8px); /* Frosted glass effect */
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease;
  }
  
  .navbar:hover {
    background: rgba(0, 123, 255, 1); /* Full blue on hover */
  }
  
  .nav-content {
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 20px;
    height: 70px;
  }
  
  /* Logo */
  .logo {
    color: white;
    text-decoration: none;
    font-size: 24px;
    font-weight: bold;
    font-family: "Poppins", sans-serif;
    transition: color 0.3s ease;
  }
  
  .logo:hover {
    color: #dfe4ea; /* Slightly lighter color on hover */
  }
  
  /* Mobile Menu Toggle */
  .menu-toggle {
    display: none;
    background: none;
    border: none;
    font-size: 28px;
    color: white;
    cursor: pointer;
  }
  
  .menu-toggle:hover {
    transform: scale(1.1);
    color: #dfe4ea; /* Lighter blue on hover */
  }
  
  /* Navigation Links */
  .nav-list {
    display: flex;
    align-items: center;
    margin: 0;
    padding: 0;
    list-style-type: none;
    gap: 20px; /* Add spacing between links */
    transition: all 0.3s ease;
  }
  
  .nav-list.open {
    display: flex;
    flex-direction: column;
    position: absolute;
    top: 70px;
    right: 0;
    background: rgba(0, 0, 0, 0.9); /* Dark transparent background */
    width: 100%;
    padding: 20px;
    border-radius: 0 0 10px 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    animation: slideDown 0.3s ease-in-out;
  }
  
  .nav-link {
    color: white;
    text-decoration: none;
    font-size: 16px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    gap: 10px; /* Space for icons */
    transition: background-color 0.3s ease, color 0.3s ease;
  }
  
  .nav-link:hover {
    background-color: rgba(255, 255, 255, 0.1);
    color: #dfe4ea;
    border-radius: 5px;
  }
  
  .nav-link .nav-icon {
    font-size: 18px;
  }
  
  /* Dropdown Menu */
  .dropdown-menu {
    display: none;
    position: absolute;
    top: 70px;
    right: 0;
    background: rgba(0, 123, 255, 0.9);
    padding: 10px 0;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
    animation: fadeIn 0.3s ease-in-out;
  }
  
  .dropdown-menu.open {
    display: block;
  }
  
  .dropdown-item {
    padding: 10px 20px;
    color: white;
    font-size: 16px;
    display: flex;
    align-items: center;
    gap: 10px;
    transition: background-color 0.3s ease;
  }
  
  .dropdown-item:hover {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 5px;
  }
  
  /* Sign-In/Sign-Out Button */
  .sign-in-button {
    background-color: white;
    color: #007bff;
    border: none;
    padding: 8px 15px;
    border-radius: 20px;
    font-weight: bold;
    cursor: pointer;
    font-size: 14px;
    transition: background-color 0.3s ease, transform 0.3s ease;
  }
  
  .sign-in-button:hover {
    background-color: #f0f0f0;
    transform: scale(1.05);
  }
  
  /* Mobile Responsive */
  @media screen and (max-width: 768px) {
    .menu-toggle {
      display: block;
    }
  
    .nav-list {
      display: none;
    }
  
    .nav-list.open {
      display: flex;
    }
  }
  
  /* Keyframe Animations */
  @keyframes fadeIn {
    from {
      opacity: 0;
      transform: translateY(-10px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  
  @keyframes slideDown {
    from {
      transform: translateY(-20px);
      opacity: 0;
    }
    to {
      transform: translateY(0);
      opacity: 1;
    }
  }
  